	border-radius: 0 0 15px 0 {
 } }		// z-index: 1

.nav-menu {
	position: fixed;
	top: 0px;
	left: 0;
	z-index: 100;
	// display: none
	max-width: 675px;
	width: 60%;
	min-width: 520px;
	max-height: 40px;
	height: 5%;
	min-height: 30px;
	margin: 0;
	padding: 0;
	list-style: none;
	// visibility: hidden
	@include flex;
	justify-content: space-between;
	// flex-direction: row-reverse
	align-items: center;
	font-family: MaryadPro;
	.nav-a {
		width: 20%;
		height: 100%;
		position: relative;
		@include transition-ease(0.5s);
		color: $white;
		text-decoration: none;
		letter-spacing: 1px;
		border-radius: 5px;
		font-size: 18px;
		div {
			width: 100%;
			text-align: center;
			position: absolute;
			top: 50%;
			left: 50%;
			@include transform(translate(-50%, -50%)); } } }

.nav-menu {
	a:hover {
		background: rgba(213, 49, 74, 0.5);
		// box-shadow: inset 0 -1px rgba(0,0,0,0)
		color: $oceanblue;
		font-weight: bold; } }


.close {
	// position: absolute
	// right: 32px
	// top: 32px
	width: 15px;
	height: 15px;
	position: relative;
	left: 10px;
	opacity: 0.7;
	animation: menu-close-rotate 7s infinite;
	@include transition-ease(1s);
	cursor: pointer; }
.close:hover {
	opacity: 1;
	// animation: none
	animation: menu-close-rotate-hover 0.5s; }


.close:before, .close:after {
	position: absolute;
	left: 7px;
	content: ' ';
	height: 15px;
	width: 2px;
	background-color: $oceanblue;
	@include transition-ease(1s); }

.close:before {
	transform: rotate(45deg); }

.close:after {
	transform: rotate(-45deg); }

.close:hover:before, .close:hover:after {
	background: $white; }






.side-panel {
	width: 30px;
	height: 100%;
	@include transition(1s);
	position: relative;
	z-index: 10;
	position: fixed;
	top: 50%;
	@include transform(translateY(-50%));
	// background: red
	// opacity: 0.6
 }	// @include animation(pulsing 2.5s infinite)

.button {
	width: 30px;
	height: 100%;
	border: none;
	background: $oceanblue;
	opacity: 0.3;
	position: fixed;
	top: 50%;
	left: 0px;
	@include transform(translateY(-50%));
	@include transition-ease(0.5s);
	z-index: 9;
	animation:  panel-color 5s infinite; }

.navpanel {
	width: 200px;
	position: fixed;
	top: 50%;
	left: -200px;
	padding-left: 20px;
	// visibility: hidden
	// left: -200px
	// opacity: 0
	@include transform(translateY(-50%));
	height: 100%;
	@include flex;
	// align-items: center
	justify-content: center;
	flex-direction: column;
	@include transition-ease(0.6s);
	a {
		display: block;
		margin: 20px 0;
		text-decoration: none;
		color: $black;
		font-size: 22px;
		@include transition-ease(0.4s);
		.works {
			left: 45px; }
		.skills {
			left: 45px; }
		.li {
			position: relative;
			width: 130px;
			display: inline-block;
			height: 120%;
			@include transition-ease(0.5s);
			border-radius: 55px;
			div {
				opacity: 1;
				@include transform(translate(14%, 0%)); } }
		.li:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 55px;
			background: $white;
			// visibility: hidden
			// opacity: .5
			@include transition-ease(0.4s); } }

	.li:hover {
		@include transition-ease(0.4s);
		@include transform(scale(1.05));
		color: $orange; }

	// a:hover
	/// color: $orange
	/// color: #064342
	//include transition(0.6s)
	a:hover .li:before {
		@include transition-ease(0.4s);
		// visibility: visible
		opacity: 0.8;

 } }		// padding: 10px 0
